            title: [" ", "Flix Yousoupoff", "Felix Yusupov"],
            text: [",     .     .      .      ,     ,  , ,     .", "Le duc Yousoupoff, ses richesses, ses palais, ses frasques. Lenfant terrible n  Saint Ptersbourg dans une famille rpute, est devenu trs tt lunique hritier de toute la fortune des Yousoupoff. En se battant contre la force sataniste et la malchance de la famille de Nicolas II, Flix sest fait connatre comme lun des principaux participants  lassassinat de Raspoutine. Aprs la Rvolution dOctobre, ayant perdu toutes les richesses familiales, Flix et sa femme se sont exils  Paris o ils ont vcu jusqu leur mort.", "The duke Yusupov, his richness, his palaces, his pranks. Born in Saint Petersburg at the most reputed family, the enfant terrible became very early the only heir of the entire Yusupovs fortune. Struggling against the satanist power and miseries of the Royal Family, Felix is known as one of the main participants of the Rasputins assassination. After the October Revolution, having lost all the family wealth, Felix and his wife escaped out of the country to Paris where they were living until their death."],
