            title: [" I   ", "Assassinat de Raspoutine", "Assassination of Rasputin"],
            text: ["     ,       .         .         .         .    30   ,  :  ,     ,  ,  ?", "Lenqute sur lun des crimes les plus rvoltants, empli de mystres. Dailleurs, on sait que lassassinat de Raspoutine a prcipit la chute des Romanov. Sinterroger sur comment le moujik russe sibrien a russi  sintroduire dans la meilleure socit de Saint Ptersbourg, jusqu tre admis dans lintimit de la famille impriale. Ecouter les opinions autour de linfluence de Raspoutine sur le Tsar et la Tsarine et son intention sur la mainmise sur lempire. Visiter le sous-sol du beau Palais Yousoupoff pour se trouver sur la scne du crime et ressentir les frissons dune exprience vibrante.", "The investigation of one of the most shocking crimes, full of mysteries. Besides, its known that Rasputins assassination triggered to the overthrow of the Romanovs. Explore how the Russian moujik from Siberia managed to enter the high society of Saint Petersburg up to the royal intimate family business. Follow the opinions of Rasputins influence on the Tsar and Tsarina and his intention to seize the power. Visit the downstairs of the magnificent Yusupov Palace to find yourselves at the crime scene that makes shiver."],
