            text: ["      ,  ,    .             ,              ,  .", "Cest  Saint Ptersbourg que la Grande Rvolution Russe a commenc, mettant fin  la monarchie Romanov, au profit de lre sovitique. Dcouvrir cette priode historiquement riche/intense qui a boulevers la vie de tout le pays et comprendre les rpercussions sur la vie du peuple que lon ressent encore aujourdhui. Exposer la diffrence entre la vie impriale et sovitique  Saint Ptersbourg et mieux comprendre la mentalit russe.", "This is right here, in Saint Petersburg, where the Grand Russian Revolution started, bringing the end to the monarchy of Romanovs and signalling the start of the soviet era. Reveal this historically intensive period that turned around the life of the whole country and find out the after-effects that one can feel in the todays way of living. Note the impacts of the imperial time and the soviet period to understand better the Russian mentality."],
