            title: ["", "Sige de Leningrad", "The siege of Leningrad"],
            text: ["   , ,      ,    21 .  ,    .         .", "Lhistoire de Saint Ptersbourg na pas toujours t heureuse. Le Sige de Lningrad a taill une place considrable dans lhistoire de ville et nous ne pouvons pas ngliger ce thme. Apprendre la vrit sur la vie quotidienne sous occupation, comprendre comment la Route de Vie sest organise et a fonctionn et dvoiler les habitudes pterbourgeoises traditionnelles, fruits de cette terrible priode.", "The destiny of Saint Petersburg has not been always in the roses. The Siege of Leningrad is an important landmark in the city history and we cant ignore this theme. Learn the true facts of the daily life under occupation, find out how the Road of Life was functioning and reveal the todays habits of local citizens, fruits of this terrible period."],
