            text: ["   :     ,    .         .        .        ,       .", "Saint Ptersbourg a vu natre la fameuse  Marque  et lcole mondialement rpute du Ballet Russe. Passez une soire dans un des thtres connus et profitez pendant lentracte de la tradition :  savourez une coupe de champagne et dguster du caviar rouge.\nFaites connaissance avec toute une vie cache, dans les coulisses du thtre illustre. Gotez le dessert  Diana Vihneva  servi uniquement dans la ville natale de lEtoile.", "Saint Petersburg gave birth to the famous brand and the worldwide known school of the Russian Ballet. Spend an evening at one of the reputed theatres and profit an intermission to follow the local tradition of a glass of hampagne and a bit of red caviar. Reveal the whole life behind the celebrated scenes hidden from the eyes.\nEnjoy the dessert Diana Vishneva served uniquely in the native city of the Etoile."],
