            title: ["  ", "Symphonie de Tchaikovsky", "Tchaikovskys symphony"],
            text: ["   ,    .     , , ?   ,   ,   , ,  ,   ?    -,     ,           .", "La priode pterbourgeoise a influenc sensiblement la vie du compositeur de gnie russe Pyotr Ilyich Tchaikovsky. Cest sur les scnes principales de Saint Ptersbourg o les premires de ses uvres les plus importantes ont lieu.\nSuivre les liens entre la biographie de la personnalit forte et le caractre imprial de la ville. Terminez le voyage par une soire au thtre Mariinsky envout par  Le lac des cygnes  ou au Philarmonie de Ptersbourg enchant par la Symphonie numro 6  Pathtique .", "The Petersburgs period influenced much the life of the Russian brilliant composer Pyotr Ilyich Tchaikovsky. His most important pieces were premiered on the main stages of Saint Petersburg.\nFigure out the strong personal history that took place in the city of the royal character. Complete your trip with an evening to enjoy the Swan Lake ballet at the Mariinsky theatre or the Symphony number 6 Pathetic - at the Saint Petersburg State Academic Philharmonic."],
