            img: ["29.jpg", "13.jpg", "13.jpg"],
            title: ["     ", "Dcor maritime de la ville : le Golfe de Finlande", "Petersburg seascape: Gulf of Finland"],
            text: ["      ,            .       , ,    ,        .", "Le Golfe de Finlande, bras de la mer Baltique, offre  Saint Ptersbourg une ouverture maritime et des axes de communications entre Helsinki et Tallinn.\nEn prenant un bateau de grande vitesse du quai  ct de lErmitage vous partirez au golf pour atteindre Pterhof, la Rsidence impriale dt, Versailles russe.\nPour percevoir latmosphre dfensive du port militaire depuis lpoque de la Russie impriale, pour voir ou visiter des forts, plus grands que le lgendaire Fort Boyard, privilgiez une escapade sur lle de Kronstadt.\nOu vadez-vous pour aller au  Kurort , station balnaire, populaire parmi les locaux (accs rapide et facile depuis le centre en train ou en voiture 30-40 min.) ette station, entoure par la fort nordique, permet de prendre un bain du soleil en t, de faire du ski de fond en hiver, de visiter la maison authentique de Rpine.", "The Gulf of Finland of the Baltic Sea gives to Saint Petersburg the access to the sea and opens the direct seaways to Helsinki and Tallinn.\nTake a high-speed boat from the quay, close to the Hermitage Museum, to enter the Gulf and to reach Peterhof, the imperial summer residence, Russian Versailles.\nTo feel the atmosphere of the military port of the Imperial Russia, to see or to visit the forts bigger than the legendary Fort Boyard, plan your trip to the island of Kronstadt.\nOr experience an escape to the Kurort, beach resort, popular among the locals (easy access from the city centre, 30-40 min. by train or by car). The resort surrounded by the northern forest offers the opportunities to take sun baths in summer, to ski in winter, to visit the authentic house of Ilya Repin.\n"],
